.load-more {
  display: none;
}

.search-form {
  max-width: 400px;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.search-form input {
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  .search-form {
    max-width: 100%;
    flex-direction: column;
  }

  .search-form input {
    margin-bottom: 10px;
  }

  .search-form button {
    margin-left: 0;
  }
}

.gallery {
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  display: grid;
}

.photo-card {
  height: 300px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  display: block;
}

.photo-card .info {
  text-align: center;
  padding: 10px;
}

.photo-card .info-item {
  margin: 5px 0;
  font-weight: bold;
}

.photo-card .info-item b {
  color: #007bff;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.load-more:hover {
  background-color: #0056b3;
}

